import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { UtilsProvider } from '../../providers/utils/utils';
import { HttpProvider } from '../../providers/http/http'
import { CommandsProvider } from '../../providers/commands/commands';

/**
 * Generated class for the AddPayBillsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-add-pay-bills',
  templateUrl: 'add-pay-bills.html',
})
export class AddPayBillsPage {

  public message: string;
  public beneficiario: string = "";
  public accountSelect: string = "";
  public cuentaCompleta: string = "";
  public reference: string = "";

  public requestSecurity: string[]; //Variable del OTP
  //obtencion del valided de security otp para no volver a pedir el OTP
  public valide: boolean;
  public procesoOTP: number=0;

  constructor(public navCtrl: NavController, public navParams: NavParams, private storage: Storage, public utils: UtilsProvider, public http: HttpProvider,
              public command: CommandsProvider) {
  
                this.valide = this.navParams.get('valided');

                if(""!=this.navParams.get('valided') && this.navParams.get('valided')!=undefined){
                  this.valide = this.navParams.get('valided');
                }
                this.storage.get('procesoOTP').then((val) => {
                  if (val != null) { 
                    this.procesoOTP = val;
                  }     
                });
                this.procesoOTP = this.utils.getProcesoOTP();

                // para cargar mas rapido las cuentas que vienen del OTP 
                this.cargarLoadAddPayBills();

                //Beneficiario seleccionado
                this.storage.get('payeeSelect').then((valor) => {
                  if(valor != null){
                    this.beneficiario = valor;
                  }
                });

                //cuenta a debitar seleccionado
                this.storage.get('accountSelect').then((valor) => {
                  if(valor != null){
                    this.accountSelect = valor;
                  }
                });

                //Cuenta completa seleccionada
                this.storage.get('cuentaCompleta').then((valor) => {
                  if(valor != null){
                    this.cuentaCompleta = valor;
                  }
                });
 
  }

  ionViewCanEnter(){  

    // para cargar mas rapido las cuentas que vienen del OTP 
    this.cargarLoadAddPayBills();

    var wait:boolean=true;
    var canView:boolean=true;
    this.procesoOTP = this.procesoOTP = this.utils.getProcesoOTP();
    this.requestSecurity = this.utils.getCommands(this.command.E2ADMR608_1);
    
    if (this.requestSecurity[1] == this.utils.opt_validator && this.procesoOTP!=1 && this.valide!=true) {
      this.loadAddPayBills(); 
      this.solicitudOTP();
      canView = false;        
    }else{
      
      this.loadAddPayBills();        
      canView = true;       
    } 
    return canView;

  }

  private solicitudOTP(){
    
    //Valor para solicitar el OTP
    this.requestSecurity = this.utils.getCommands(this.command.E2ADMR608_1);
    this.valide = this.navParams.get('valided');

    //Obtencion de la variable de sesion para no volver a pedir el OTP
    this.storage.get('procesoOTP').then((val) => {
      if (val != null) {
        this.procesoOTP = val;
      }
      this.navToOTP();
    });

  }

  private navToOTP(){    

    if (this.requestSecurity[1] == '*' && this.procesoOTP!=1 && this.valide!=true) {
      this.navCtrl.push("SecurityOtpPage",{irPantalla: "AddPayBillsPage",  cmdAux: 'E2ADMR608_1' }); 
    }

  } 

  //metodo para cargar las listas enviadas desde security OTP la cual antes se cargaba en loadAddPayBills
  public cargarLoadAddPayBills(){

    this.storage.get('cuentasObtenidasOTP_AddPayBills_1').then((val) =>    { 
      this.storage.set("selectPayee", val);
    });

    this.storage.get('cuentasObtenidasOTP_AddPayBills_2').then((val) =>    { 
      this.storage.set("accountToDebit", val);
    });        

  }

  selectPayee() { 
    this.navCtrl.push("SelectPayeePage"); 
  }

  selectAccountDebit() { 
    this.navCtrl.push("SelectAccDebitPage"); 
  }

  
  private loadAddPayBills() {    
        
    this.cargarLoadAddPayBills();  
    //var command = 'E2ADMR608_1';
    var command :string[] =  this.utils.getCommands(this.command.E2ADMR608_1);

    //Entra la primera vez y necesita OTP realizar el CALL
    if(this.requestSecurity[1] == this.utils.opt_validator && this.procesoOTP!=1 && this.valide!=true)
    {
      if (this.utils.getCommands != null && this.utils.getCommands != undefined && this.utils.getCommands.length > 0) 
      {
        var param = command[0] + '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+'&E2EW=null' + this.utils.separator + ' ' + this.utils.separator + this.utils.separator + 'E2NCNR01' + this.utils.separator + 'null' + this.utils.separator + '%BA%20&PAR=' + this.utils.separator;
        this.http.callServer(param).then(res => {      
          this.message = res.message;
      
            if (this.message != '' && typeof this.message!=="undefined") {
              this.utils.alertServidor(this.message);
            } else {
              this.cargarLoadAddPayBills();        
            }
          });
      }
    }//Entra proveniente del securiti-otp y ya los datos estan en sesion
    else if(this.requestSecurity[1] == this.utils.opt_validator && this.procesoOTP==1)
    {
      this.cargarLoadAddPayBills();  
    }//Entra cuando no se necesita hacer llamado al OTP y hace proceso completo
    else if(this.requestSecurity[1] != this.utils.opt_validator)
    { 
         if (this.utils.getCommands != null && this.utils.getCommands != undefined && this.utils.getCommands.length > 0) 
         {
            var param = command[0] + '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+'&E2EW=null' + this.utils.separator + ' ' + this.utils.separator + this.utils.separator + 'E2NCNR01' + this.utils.separator + 'null' + this.utils.separator + '%BA%20&PAR=' + this.utils.separator;
            this.http.callServer(param).then(res => {      
            this.message = res.message;

              if (this.message != '' && typeof this.message!=="undefined") {
                this.utils.alertServidor(this.message);
              } else {
                this.storage.set("selectPayee", res.lista1);
                this.storage.set("accountToDebit", res.lista2);         
              }
            },
              error => {
                console.log(error);
              });
          }
    }
    
    
  }


  //Metodo validar datos
  public validarDatos(){
  
    if(this.beneficiario == undefined || this.beneficiario == "" || this.accountSelect == undefined || this.accountSelect == "" || this.reference == undefined || this.reference == "" ){
      this.utils.alertCampos("productToPay.alert");
    }
    else{
      this.agregarPayBills();
    }
  
}


//Metodo para validar que solo ingrese numeros en el campo mediante js
validaNumericos(event) {
  if (event.charCode >= 48 && event.charCode <= 57) {
    return true;
  }
  return false;
}

//Metodo para agregar cuentas
public agregarPayBills(){

  if(this.beneficiario != undefined)                               { var benefici = this.beneficiario; } else { benefici = "" }
  if(this.accountSelect != undefined || this.accountSelect != "" ) { var account = this.cuentaCompleta; } else { account = "" }
  if(this.reference != undefined || this.reference != "" )         { var ref = this.reference;         } else { ref = "" }
      
  var nickName = "";
  
  var command = 'E2ADMR608_1';
  var param = command + '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+'&E2EW=null' + this.utils.separator + 'Enviar' + 
              this.utils.separator + this.utils.separator + 'E2LSMR79_1' + this.utils.separator + '0' + this.utils.separator + this.utils.separator + '&PAR='+ benefici + 
              this.utils.separator + account + this.utils.separator + ref + this.utils.separator + nickName + this.utils.separator;
  this.http.callServer(param).then(res => {      
  this.message = res.message;

    if (this.message != '' && typeof this.message!=="undefined") {
      //alert(this.message);
      this.utils.alertCampos("addAccount.message");
      this.navCtrl.push("RutaPagosPage", { cargarNuevaFactura: 1 });
    } else {
      //var res = res.lista1;                
    }
  },
    error => {
      console.log(error);
    });
}

logout(){
  this.utils.presentConfirm("LoginPage");
}

public cancelar(){ 
  //this.utils.cleanAll();
  this.navCtrl.push("RutaPagosPage"); 
}


}



